import React, { useState } from 'react';
import DropdownFilter from '@components/Catalog/components/DropdownFilter';
import { FilterContainer } from './style';
const sizeArr = [
    { value: 'L', label: 'L' },
    { value: 'S', label: 'S' },
    { value: 'M', label: 'M' },
];
const colorArr = [
    { value: 'white', label: '' },
    { value: 'blue', label: '' },
    { value: 'yellow', label: '' },
];
const priceArr = [
    { value: 'expensive', label: '' },
    { value: 'cheap', label: '' },
];
const sortArr = [
    { value: 'popularity', label: '' },
    { value: 'estimation', label: '' },
    { value: 'discount', label: '' },
];
const Filter = () => {
    const [size, setSize] = useState<string>();
    const [color, setColor] = useState<string>();
    const [price, setPrice] = useState<string>();
    const [sort, setSort] = useState<string>();
    return (
        <FilterContainer>
            <DropdownFilter width={79} value={size} onChange={setSize} options={sizeArr} placeholder="" />
            <DropdownFilter width={60} value={color} onChange={setColor} options={colorArr} placeholder="" />
            <DropdownFilter width={62} value={price} onChange={setPrice} options={priceArr} placeholder="" />
            <DropdownFilter
                width={145}
                value={sort}
                onChange={setSort}
                options={sortArr}
                placeholder=" "
            />
        </FilterContainer>
    );
};

export default Filter;
